








































import Vue from "vue";
import Article from "@/components/knowledge/Article.vue";
import { group, writings } from "@/scripts/fabric/writings";

const welcomeId = 101;

export default Vue.extend({
  components: {
    Article
  },
  data() {
    return {
      source: "",
      selectId: 0,
      open: new Map<number, boolean>()
    };
  },
  computed: {
    group() {
      return group;
    },
    writings() {
      return writings;
    }
  },
  created() {
    this.init();
  },
  beforeRouteUpdate(to, from, next) {
    const toId = to.query.id as string;
    const fromId = from.query.id;
    if (toId !== fromId) {
      this.getWriting(Number(toId));
    }
    next();
  },
  methods: {
    init() {
      const id = this.$route.query.id as string;
      this.getWriting(Number(id));
    },
    getWriting(id: number) {
      if (!id) {
        this.jumpWriting(welcomeId);
        return;
      }
      const article = this.writings.find(e => e.id === id);
      if (!article) {
        this.jumpWriting(welcomeId);
        return;
      }
      this.selectId = id;

      Promise.resolve()
        .then(() => {
          // read writing from assets
          const w = require(`@/assets${article.path}`);
          return w.default;
        })
        .then((res: any) => {
          if (process.env.VUE_APP_PUBLIC_PATH) {
            res = res.replace(
              /\/book\//g,
              `${process.env.VUE_APP_PUBLIC_PATH}book/`
            );
          }
          this.source = res;
          return this.$nextTick();
        })
        .then(() => {
          const rightComp: any = this.$refs.right;
          rightComp.scrollTop = 0;
        })
        .catch((err: Error) => {
          //console.error(err);
        });
    },
    isOpen(id: number) {
      return !!this.open.get(id);
    },
    clickGroup(id: number) {
      const status = !this.isOpen(id);
      this.open.set(id, status);
      this.$forceUpdate();
    },

    getWritingsByGroupId(id: number) {
      return this.writings.filter(e => e.groupId === id);
    },
    clickWriting(id: number) {
      this.jumpWriting(id);
    },
    jumpWriting(id: number) {
      this.$router.push({ path: "/fabric", query: { id: id.toString() } });
    }
  }
});
